	module.exports = {"pathContext":{"group":[{"node":{"id":"3a5dc571-92ef-5aef-a94b-34e86f0df236","slug":"third-to-half-of-uk-population-lives-with-chronic-pain","title":"Third to half of UK population lives with chronic pain","excerpt":"<p>Proportion likely to rise as population ages; major cause of disability and distress Between a third and half (43%) of the UK population—roughly 28 million adults—lives with chronic pain, finds an analysis of the available evidence, published in the online journal BMJ Open. This proportion is likely to rise as the population ages, warn the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/06/20/third-to-half-of-uk-population-lives-with-chronic-pain/\">Read More&#8230;</a></p>\n","status":"publish","date":"June 20, 2016","template":"","format":"standard"}},{"node":{"id":"ea054f60-a2c3-5356-ab75-ac9ca3e71881","slug":"drug-treatment-of-hyperactivity-in-kids-may-have-levelled-off-in-uk","title":"Drug treatment of hyperactivity in kids may have levelled off in UK","excerpt":"<p>But it lasts much longer than it does in rest of Europe or US The tendency to treat childhood hyperactivity (ADHD) with drugs may have reached a plateau in the UK, following a steep rise in the number of prescriptions for these medicines over the past 20 years, reveals research published in the online journal BMJ [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/06/20/drug-treatment-of-hyperactivity-in-kids-may-have-levelled-off-in-uk/\">Read More&#8230;</a></p>\n","status":"publish","date":"June 20, 2016","template":"","format":"standard"}},{"node":{"id":"f8c8520e-4b0a-58fc-9a1e-094dc4b4adc5","slug":"prolonged-sitting-non-fatal-self-harm-and-institutional-mental-healthcare-most-read-articles-in-may","title":"Prolonged sitting, non-fatal self-harm, and institutional mental healthcare: Most read articles in May","excerpt":"<p>Prolonged sitting and fatigue, non-fatal self-harm, and trends in institutional mental healthcare in Western Europe This month sees a mixture of some old favourites and some new entries in the Top 10 Most Read list. First up is a pilot study by Wennberg et al which uses a randomised two-condition crossover trial to compare the acute effects of uninterrupted sitting [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/06/06/prolonged-sitting-non-fatal-self-harm-and-institutional-mental-healthcare-most-read-articles-in-may/\">Read More&#8230;</a></p>\n","status":"publish","date":"June 06, 2016","template":"","format":"standard"}},{"node":{"id":"f09c9288-69d3-5183-a285-6d6f168ecf3d","slug":"international-clinical-trials-day","title":"International Clinical Trials Day","excerpt":"<p>Today is Clinical Trials Day. Celebrated around the world on or near May 20, Clinical Trials Day commemorates the date when, in 1747, James Lind started the first clinical trial to test citrus fruits as a treatment for scurvy. This year&#8217;s campaign from the NIHR is OK To Ask, which encourages patients and carers to inquire about research opportunities [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/05/19/international-clinical-trials-day/\">Read More&#8230;</a></p>\n","status":"publish","date":"May 19, 2016","template":"","format":"standard"}},{"node":{"id":"23b1e74b-038c-581a-a505-b059fd9d73c2","slug":"celebrating-international-nurses-day-ind2016","title":"Celebrating International Nurses Day #IND2016","excerpt":"<p>&nbsp; International Nurses Day, celebrated on 12th May each year and coinciding with Florence Nightingale&#8217;s birthday, marks the important contributions nurses make to society. As the single largest group of health professionals, nurses care from the first moments of life right through to the final days. To show our appreciation of the work that nurses [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/05/12/celebrating-international-nurses-day-ind2016/\">Read More&#8230;</a></p>\n","status":"publish","date":"May 12, 2016","template":"","format":"standard"}},{"node":{"id":"52d76cdc-c66f-5b02-b7fa-1d61ca469fc9","slug":"radiotherapy-during-surgery-could-save-millions-of-travel-miles-and-tonnes-of-co2","title":"Radiotherapy during surgery could save millions of travel miles and tonnes of CO2","excerpt":"<p>…plus free up thousands of hours for women with early stage breast cancer, every year One targeted dose of radiotherapy given during surgery to remove early stage breast cancer could save millions of travel miles, enough CO2 emissions for a 100 hectare forest, and free up thousands of hours of women’s time, concludes research published [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/05/09/radiotherapy-during-surgery-could-save-millions-of-travel-miles-and-tonnes-of-co2/\">Read More&#8230;</a></p>\n","status":"publish","date":"May 09, 2016","template":"","format":"standard"}},{"node":{"id":"a90ad787-e8e0-5142-b92a-5c9f5c9490a7","slug":"prenatal-exposure-to-cannabis-overdiagnosis-and-counterfeit-medicines-most-read-articles-in-april","title":"Prenatal exposure to cannabis, overdiagnosis, and counterfeit medicines: Most read articles in April","excerpt":"<p>Prenatal exposure to cannabis, public definitions of overdiagnosis, and counterfeit medicines in Peru The Top 10 Most Read papers list this month contains a number of new entries, many of which have been published within the current issue. They come from a wide range of of areas and some have attracted attention via press release, blogs and discussion [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/05/09/prenatal-exposure-to-cannabis-overdiagnosis-and-counterfeit-medicines-most-read-articles-in-april/\">Read More&#8230;</a></p>\n","status":"publish","date":"May 09, 2016","template":"","format":"standard"}},{"node":{"id":"0fed5230-00a7-5d86-be90-9719bd509a37","slug":"hospital-self-harm-cases-have-steadily-risen-among-men-in-england-since-2008","title":"Hospital self-harm cases have steadily risen among men in England since 2008","excerpt":"<p>But only half of those going to hospital afterwards get mental health assessment The number of hospital cases of self-inflicted harm, such as cutting and overdosing on prescription meds, has risen steadily since 2008 in England among men, reveals research published in the online journal BMJ Open. But only around half of those who go [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/04/28/hospital-self-harm-cases-have-steadily-risen-among-men-in-england-since-2008/\">Read More&#8230;</a></p>\n","status":"publish","date":"April 28, 2016","template":"","format":"standard"}},{"node":{"id":"1249c8cf-0cf8-5e50-8ff6-d3ac45d4869a","slug":"metal-hip-replacements-implanted-since-2006-more-prone-to-failure","title":"Metal hip replacements implanted since 2006 more prone to failure","excerpt":"<p>Higher rate of issues in manufacturing process since this date may be to blame, say researchers Metal on metal hip replacements implanted since 2006 are more prone to failure and the need for further surgery, finds research looking at revision rates at one hospital trust for the DePuy Pinnacle device, and published in the online [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/04/28/metal-hip-replacements-implanted-since-2006-more-prone-to-failure/\">Read More&#8230;</a></p>\n","status":"publish","date":"April 28, 2016","template":"","format":"standard"}},{"node":{"id":"06eeeebb-0d1e-54f1-8117-e3d126a8edb0","slug":"what-is-overdiagnosis-public-meanings-and-misconceptions","title":"What is ‘overdiagnosis’? Public meanings and misconceptions","excerpt":"<p>A recent study published in BMJ Open found less than 3% of the British public could define the meaning of overdiagnosis correctly. We talk to lead author Dr Alex Ghanouni about his research. Stories about the ‘overdiagnosis’ of medical conditions regularly make the headlines. “GPs to trial new tests for asthma amid concerns about overdiagnosis” [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/04/28/what-is-overdiagnosis-public-meanings-and-misconceptions/\">Read More&#8230;</a></p>\n","status":"publish","date":"April 28, 2016","template":"","format":"standard"}}],"pathPrefix":"posts","first":false,"last":false,"index":7,"pageCount":26,"additionalContext":{}}}

